"use client"

import { useState } from "react"
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query"
import { 
  Package, 
  Milk, 
  Check, 
  X, 
  Phone, 
  MapPin, 
  ChevronDown,
  ChevronUp,
  Download,
  RefreshCw,
  Minus,
  Plus
} from "lucide-react"
import { Card, Button, Badge, SectionLoader, EmptyState } from "@/components/ui"
import { formatQuantity, DELIVERY_STATUS_LABELS } from "@/lib/constants"
import { formatPhone } from "@/lib/utils"
import toast from "react-hot-toast"

export default function DeliveryDashboard() {
  const queryClient = useQueryClient()
  const [expandedId, setExpandedId] = useState<string | null>(null)
  const [bottleInputs, setBottleInputs] = useState<Record<string, { large: number; small: number }>>({})

  const { data: summaryData, isLoading: summaryLoading } = useQuery({
    queryKey: ["delivery-summary"],
    queryFn: async () => {
      const res = await fetch("/api/delivery/summary")
      if (!res.ok) throw new Error("Failed to fetch")
      return res.json()
    },
  })

  const { data: deliveryData, isLoading: deliveryLoading, refetch } = useQuery({
    queryKey: ["today-deliveries"],
    queryFn: async () => {
      const res = await fetch("/api/delivery/today")
      if (!res.ok) throw new Error("Failed to fetch")
      return res.json()
    },
  })

  const markDelivery = useMutation({
    mutationFn: async ({ id, status, large, small }: { 
      id: string; 
      status: "DELIVERED" | "NOT_DELIVERED";
      large: number;
      small: number;
    }) => {
      const res = await fetch(`/api/delivery/${id}/mark`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          status,
          largeBottlesCollected: large,
          smallBottlesCollected: small,
        }),
      })
      if (!res.ok) {
        const data = await res.json()
        throw new Error(data.error)
      }
      return res.json()
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["today-deliveries"] })
      queryClient.invalidateQueries({ queryKey: ["delivery-summary"] })
      toast.success("Delivery updated")
      setExpandedId(null)
    },
    onError: (err: any) => {
      toast.error(err.message)
    },
  })

  const handleExport = () => {
    window.open("/api/delivery/export", "_blank")
  }

  if (summaryLoading || deliveryLoading) return <SectionLoader />

  const summary = summaryData?.data?.loadSummary
  const deliveries = deliveryData?.data?.deliveries || []
  const stats = deliveryData?.data?.summary

  const scheduledDeliveries = deliveries.filter((d: any) => d.status === "SCHEDULED")
  const completedDeliveries = deliveries.filter((d: any) => d.status !== "SCHEDULED")

  const getBottleInput = (id: string) => {
    return bottleInputs[id] || { large: 0, small: 0 }
  }

  const updateBottleInput = (id: string, type: "large" | "small", delta: number) => {
    const current = getBottleInput(id)
    const newValue = Math.max(0, current[type] + delta)
    setBottleInputs({ ...bottleInputs, [id]: { ...current, [type]: newValue } })
  }

  return (
    <div className="p-4 pb-20">
      {/* Date */}
      <div className="flex items-center justify-between mb-4">
        <div>
          <p className="text-sm text-neutral-500">Today's Deliveries</p>
          <p className="font-semibold text-neutral-900">
            {new Date().toLocaleDateString('en-IN', { 
              weekday: 'long', 
              day: 'numeric', 
              month: 'short' 
            })}
          </p>
        </div>
        <div className="flex gap-2">
          <button
            onClick={() => refetch()}
            className="h-10 w-10 flex items-center justify-center rounded-xl bg-neutral-100"
          >
            <RefreshCw className="h-5 w-5 text-neutral-600" />
          </button>
          <button
            onClick={handleExport}
            className="h-10 w-10 flex items-center justify-center rounded-xl bg-neutral-100"
          >
            <Download className="h-5 w-5 text-neutral-600" />
          </button>
        </div>
      </div>

      {/* Load Summary */}
      <Card className="bg-primary-600 text-white mb-4">
        <p className="text-primary-100 text-sm mb-2">Today's Load</p>
        <div className="flex items-center gap-6">
          <div className="flex items-center gap-3">
            <div className="h-12 w-12 bg-white/20 rounded-xl flex items-center justify-center">
              <Milk className="h-6 w-6" />
            </div>
            <div>
              <p className="text-2xl font-bold">{summary?.largeBottles || 0}</p>
              <p className="text-xs text-primary-100">1L Bottles</p>
            </div>
          </div>
          <div className="flex items-center gap-3">
            <div className="h-12 w-12 bg-white/20 rounded-xl flex items-center justify-center">
              <Milk className="h-5 w-5" />
            </div>
            <div>
              <p className="text-2xl font-bold">{summary?.smallBottles || 0}</p>
              <p className="text-xs text-primary-100">500ml Bottles</p>
            </div>
          </div>
        </div>
        <div className="flex items-center justify-between mt-4 pt-4 border-t border-white/20 text-sm">
          <span className="text-primary-100">Total Deliveries</span>
          <span className="font-semibold">{summary?.totalDeliveries || 0}</span>
        </div>
      </Card>

      {/* Stats */}
      <div className="grid grid-cols-3 gap-3 mb-4">
        <Card padding="sm" className="text-center">
          <p className="text-2xl font-bold text-neutral-900">{stats?.scheduled || 0}</p>
          <p className="text-xs text-neutral-500">Pending</p>
        </Card>
        <Card padding="sm" className="text-center bg-green-50 border-green-200">
          <p className="text-2xl font-bold text-green-600">{stats?.delivered || 0}</p>
          <p className="text-xs text-green-600">Done</p>
        </Card>
        <Card padding="sm" className="text-center bg-amber-50 border-amber-200">
          <p className="text-2xl font-bold text-amber-600">{stats?.paused || 0}</p>
          <p className="text-xs text-amber-600">Paused</p>
        </Card>
      </div>

      {/* Pending Deliveries */}
      {scheduledDeliveries.length > 0 && (
        <div className="mb-6">
          <h2 className="font-semibold text-neutral-900 mb-3">
            Pending ({scheduledDeliveries.length})
          </h2>
          
          <div className="space-y-3">
            {scheduledDeliveries.map((delivery: any) => {
              const isExpanded = expandedId === delivery.id
              const bottles = getBottleInput(delivery.id)
              const customerBottles = delivery.customerBottleBalance || { largeBottles: 0, smallBottles: 0 }

              return (
                <Card key={delivery.id} padding="none" className="overflow-hidden">
                  {/* Header */}
                  <button
                    onClick={() => setExpandedId(isExpanded ? null : delivery.id)}
                    className="w-full p-4 text-left"
                  >
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="font-semibold text-neutral-900">
                          {delivery.customer.name}
                        </p>
                        <p className="text-sm text-neutral-500">
                          {formatQuantity(delivery.quantityMl)} · 
                          {delivery.largeBottles > 0 && ` ${delivery.largeBottles}×1L`}
                          {delivery.smallBottles > 0 && ` ${delivery.smallBottles}×500ml`}
                        </p>
                      </div>
                      <div className="flex items-center gap-2">
                        {(customerBottles.largeBottles > 0 || customerBottles.smallBottles > 0) && (
                          <Badge variant="warning" size="sm">
                            <Package className="h-3 w-3 mr-1" />
                            {customerBottles.largeBottles + customerBottles.smallBottles}
                          </Badge>
                        )}
                        {isExpanded ? (
                          <ChevronUp className="h-5 w-5 text-neutral-400" />
                        ) : (
                          <ChevronDown className="h-5 w-5 text-neutral-400" />
                        )}
                      </div>
                    </div>
                  </button>

                  {/* Expanded Content */}
                  {isExpanded && (
                    <div className="px-4 pb-4 border-t border-neutral-100 pt-4 animate-slideDown">
                      {/* Customer Info */}
                      <div className="space-y-2 mb-4">
                        <a
                          href={`tel:${delivery.customer.phone}`}
                          className="flex items-center gap-2 text-primary-600"
                        >
                          <Phone className="h-4 w-4" />
                          <span className="text-sm">{formatPhone(delivery.customer.phone)}</span>
                        </a>
                        <div className="flex items-start gap-2 text-neutral-600">
                          <MapPin className="h-4 w-4 mt-0.5 shrink-0" />
                          <span className="text-sm">
                            {delivery.customer.addressLine1}
                            {delivery.customer.addressLine2 && `, ${delivery.customer.addressLine2}`}
                            {delivery.customer.landmark && ` (${delivery.customer.landmark})`}
                          </span>
                        </div>
                        {delivery.customer.deliveryNotes && (
                          <p className="text-sm text-amber-600 bg-amber-50 p-2 rounded-lg">
                            Note: {delivery.customer.deliveryNotes}
                          </p>
                        )}
                      </div>

                      {/* Bottles to Collect */}
                      {(customerBottles.largeBottles > 0 || customerBottles.smallBottles > 0) && (
                        <div className="mb-4 p-3 bg-amber-50 rounded-xl">
                          <p className="text-sm font-medium text-amber-800 mb-2">
                            Empty bottles with customer:
                          </p>
                          <div className="flex gap-4 text-sm text-amber-700">
                            {customerBottles.largeBottles > 0 && (
                              <span>{customerBottles.largeBottles}× 1L</span>
                            )}
                            {customerBottles.smallBottles > 0 && (
                              <span>{customerBottles.smallBottles}× 500ml</span>
                            )}
                          </div>
                        </div>
                      )}

                      {/* Bottle Collection Input */}
                      <div className="mb-4">
                        <p className="text-sm font-medium text-neutral-700 mb-2">
                          Bottles Collected
                        </p>
                        <div className="grid grid-cols-2 gap-3">
                          {/* Large Bottles */}
                          <div className="flex items-center justify-between p-3 bg-neutral-50 rounded-xl">
                            <span className="text-sm text-neutral-600">1L</span>
                            <div className="flex items-center gap-2">
                              <button
                                onClick={() => updateBottleInput(delivery.id, "large", -1)}
                                className="h-8 w-8 rounded-lg bg-white border flex items-center justify-center"
                              >
                                <Minus className="h-4 w-4" />
                              </button>
                              <span className="w-8 text-center font-semibold">{bottles.large}</span>
                              <button
                                onClick={() => updateBottleInput(delivery.id, "large", 1)}
                                className="h-8 w-8 rounded-lg bg-white border flex items-center justify-center"
                              >
                                <Plus className="h-4 w-4" />
                              </button>
                            </div>
                          </div>

                          {/* Small Bottles */}
                          <div className="flex items-center justify-between p-3 bg-neutral-50 rounded-xl">
                            <span className="text-sm text-neutral-600">500ml</span>
                            <div className="flex items-center gap-2">
                              <button
                                onClick={() => updateBottleInput(delivery.id, "small", -1)}
                                className="h-8 w-8 rounded-lg bg-white border flex items-center justify-center"
                              >
                                <Minus className="h-4 w-4" />
                              </button>
                              <span className="w-8 text-center font-semibold">{bottles.small}</span>
                              <button
                                onClick={() => updateBottleInput(delivery.id, "small", 1)}
                                className="h-8 w-8 rounded-lg bg-white border flex items-center justify-center"
                              >
                                <Plus className="h-4 w-4" />
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>

                      {/* Action Buttons */}
                      <div className="flex gap-3">
                        <Button
                          variant="outline"
                          onClick={() => markDelivery.mutate({
                            id: delivery.id,
                            status: "NOT_DELIVERED",
                            large: bottles.large,
                            small: bottles.small
                          })}
                          loading={markDelivery.isPending}
                          className="flex-1 border-red-200 text-red-600 hover:bg-red-50"
                        >
                          <X className="h-4 w-4" />
                          Not Done
                        </Button>
                        <Button
                          onClick={() => markDelivery.mutate({
                            id: delivery.id,
                            status: "DELIVERED",
                            large: bottles.large,
                            small: bottles.small
                          })}
                          loading={markDelivery.isPending}
                          className="flex-1"
                        >
                          <Check className="h-4 w-4" />
                          Delivered
                        </Button>
                      </div>
                    </div>
                  )}
                </Card>
              )
            })}
          </div>
        </div>
      )}

      {/* Completed Deliveries */}
      {completedDeliveries.length > 0 && (
        <div>
          <h2 className="font-semibold text-neutral-900 mb-3">
            Completed ({completedDeliveries.length})
          </h2>
          
          <div className="space-y-2">
            {completedDeliveries.map((delivery: any) => (
              <Card key={delivery.id} padding="sm">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="font-medium text-neutral-900">{delivery.customer.name}</p>
                    <p className="text-sm text-neutral-500">
                      {formatQuantity(delivery.quantityMl)}
                    </p>
                  </div>
                  <Badge
                    variant={
                      delivery.status === "DELIVERED" ? "success" :
                      delivery.status === "PAUSED" || delivery.status === "HOLIDAY" ? "warning" :
                      "error"
                    }
                  >
                    {DELIVERY_STATUS_LABELS[delivery.status as keyof typeof DELIVERY_STATUS_LABELS]}
                  </Badge>
                </div>
              </Card>
            ))}
          </div>
        </div>
      )}

      {/* Empty State */}
      {deliveries.length === 0 && (
        <EmptyState
          icon={Package}
          title="No deliveries today"
          description="Check back tomorrow morning for your delivery list."
        />
      )}
    </div>
  )
}
